import React from "react";
import {
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalFooter,
  ModalBody,
  Button,
} from "@chakra-ui/react";

const ThankYouModal = ({ isOpen, onClose, totalPeoples }) => {
  return (
    <Modal isOpen={isOpen} onClose={onClose}>
      <ModalOverlay />
      <ModalContent>
        <ModalHeader>Agradecemos sua presena!</ModalHeader>
        <ModalBody>
          Obrigado pela confirmao de presena. Estamos ansiosos para v-lo{totalPeoples == 1 ? "" : "s"}!
        </ModalBody>
        <ModalFooter>
          <Button colorScheme="blue" onClick={onClose}>
            Fechar
          </Button>
        </ModalFooter>
      </ModalContent>
    </Modal>
  );
};

export default ThankYouModal;
